<template>
  <div class="about">
    <div class="about__txt" ref="txt">
      <h1>About</h1>
      <p>The great shooter games of the past have inspired us to produce a P2E game design that will allow you to take your Bro into the fray. We have developed a beta version to keep you busy until we mint and move onto full game development with NFT integration.
      Gunner Bros will have a rich full storyline and gameplay designed and written by our creative lead and national bestselling author. There will also be a token asset, Brass, which will be collectable in game and tradable with other players for loot, upgrades and retro gaming collectables</p>
