<template>
  <div class="home">
    <Intro v-if="!introFinished" v-on:skipIntro="skipIntroHandler"/>
    <Site v-else/>
  </div>
</template>

<script>
// @ is an alias to /src
import Intro from '@/components/Intro.vue'
import Site from '@/components/site/Site.vue'

export default {
  name: 'Home',
  components: {
    Intro,
    Site
  },
  data () {
    return {
      introFinished: false
    }
  },
  methods: {
    skipIntroHandler () {
      this.introFinished = true
    }
  }

}
</script>
