<template>
  <div ref='header' class='header' :style="{'height': `${headerHeight}px`}">
    <div ref="sun" class="sun"></div>
    <div ref="clouds" class="clouds"></div>
    <div ref="mountains" class="mountains"></div>
    <div ref="middleground" class="middleground"></div>
    <div ref="foreground" class="foreground"></div>
    <!-- <div class="figures">
      <img class="zeke"/>
      <img class="blaze"/>
    </div> -->
  </div>
</template>

<script>
import { gsap } from 'gsap'
// import { gsap, Linear } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

export default {
  data () {
    return {
      headerHeight: 300,
      startClounds: 0,
      cloundIdx: 0,
      count4: 2,
      onlyOneCall: true
    }
  },
  mounted () {
    const me = this
    this.headerHeight = this.$refs.foreground.clientHeight
    this.createClound(2, (this.$refs.foreground.clientWidth / 5))
    this.createClound(1, (this.$refs.foreground.clientWidth / 2) + 3)
    gsap.timeline({
      scrollTrigger: {
        trigger: '#header',
        start: 'top',
        // markers: true,
        scrub: true
      }
    })
      .fromTo(this.$refs.sun, { y: 0 }, { y: 400 }, 0)
      .fromTo(this.$refs.clouds, { y: 0 }, { y: 300 }, 0)
      .fromTo(this.$refs.mountains, { y: 0 }, { y: 200 }, 0)
      .fromTo(this.$refs.middleground, { y: 0 }, { y: 100 }, 0)

    window.addEventListener('resize', () => {
      me.headerHeight = me.$refs.foreground.clientHeight
    })
  },
  methods: {
    tickCreateCloud () {
      this.createClound(this.count4, this.$refs.foreground.clientWidth)
      if (++this.count4 > 4) this.count4 = 1
    },
    createClound (cloudNumber, startPos) {
      const clound = document.createElement('div')

      const refIn = `cloud${this.cloundIdx}`
      clound.setAttribute('id', refIn)
      clound.setAttribute('class', `cloud${cloudNumber}`)
      this.$refs.clouds.appendChild(clound)
      this.cloundIdx++
      gsap.fromTo('#' + refIn, { x: startPos }, {
        x: 0,
        duration: 100,
        ease: 'none',
        onUpdate: () => {
          if (gsap.getProperty(`#${refIn}`, 'x').toFixed(0) === (this.$refs.foreground.clientWidth / 2).toFixed(0) && this.onlyOneCall) {
            console.log(refIn, gsap.getProperty(`#${refIn}`, 'x'))
            this.tickCreateCloud()
            this.onlyOneCall = false
            setTimeout(() => {
              this.onlyOneCall = true
            }, 4000)
          }
        },
        onComplete: () => {
          this.$refs.clouds.removeChild(clound)
        }
      })
    }
  }
}
</script>
