    background-image: url($url);
    background-repeat: no-repeat;
    background-size: 27%;
    position: absolute;
    width: 100%;
    aspect-ratio: 16/9;
  }
  @mixin bros($url) {
    content:url($url);
    width: 100%;
    position: absolute;
    aspect-ratio: 16/9;
  }

  .header {
    @include bg('../../assets/header/sky.png');
    position: relative;
    overflow: hidden;
    .sun {
      // @include bros('../../assets/header/sun.png');
      background-image: url('../../assets/header/sun.png');
      background-size: 27%;
      background-position: center 5px;
      background-repeat: no-repeat;
      width: 100%;
      position: absolute;
      aspect-ratio: 16/9;
      z-index: 1;
    }
    .clouds {
      // background-image: url('../../assets/header/clouds.png');
      // background-position: center;
      // background-repeat: no-repeat;
      // background-size: cover;

      width: 100%;
      position: absolute;
      aspect-ratio: 16/9;
      // aspect-ratio: 476/135;
      z-index: 2;
    }
    .cloud1 {
      @include cloudFac('../../assets/header/cloud_01.png');
      background-position: left 18%;
    }
    .cloud2 {
      @include cloudFac('../../assets/header/cloud_02.png');
      background-position: left 26%;
    }
    .cloud3 {
      @include cloudFac('../../assets/header/cloud_03.png');
      background-position: left 16%;
    }
    .cloud4 {
      width: 100%;
      @include cloudFac('../../assets/header/cloud_04.png');
      background-position: left 30%;
    }
    .mountains {
      @include bros('../../assets/header/mountains.png');
      z-index: 3;
    }
    .middleground {
      @include bros('../../assets/header/middle-ground.png');
      z-index: 4;
    }
    .foreground {
      @include bros('../../assets/header/foreground.png');
      z-index: 5;
      // background-position: bottom;
      // background-size: cover;
    }
  }

@media(max-width: 800px) {
  .zeke, .blaze {
    width: 50vw;
  }
}
