<template>
<div class="welcome">
  <img class="welcome__img" ref="img" :src="bro" alt="">
  <div class="welcome__txt" ref="txt">
    <h1 class="welcome__txt--title">welcome</h1>
    <p class="welcome__txt--txt">Gunner Bros is a collection of 9,999 unique Bros, and their guns, designed to re-ignite the nostalgia of retro gunner games from our past. These Bros have arrived ready to chew gum and kick some ass, and we are fresh out of gum. Sure they may have missed leg day but try to get in their way and you will regret it. Zeke and Blaze have a massive arsenal to choose from, and you may even find them sporting a set of ray-bans or bringing out a BFG
    </p>
  </div>
</div>
