<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

const _bro = require('@/assets/white-bro_finalFight.png')

export default {
  data () {
    return {
      bro: _bro
    }
  },
  mounted () {
    gsap.timeline({
      scrollTrigger: {
        trigger: '#header',
        start: 'top',
        // markers: true,
        scrub: true
      }
    })
      .fromTo(this.$refs.img, { autoAlpha: 0, x: -100 }, { autoAlpha: 1, x: 0 }, 0)
      .fromTo(this.$refs.txt, { autoAlpha: 0, x: 100 }, { autoAlpha: 1, x: 0 }, 0)
  }
}
</script>
