<template>
  <div>
    <Nav v-on:menuClick="gotoSection"/>
    <div class="sections">
      <section id="header" ref="header"><Gheader/></section>
      <div class="sizedBody">
        <section id="welcome" ref="welcome"><Welcome/></section>
        <section id="about" ref="about"><About/></section>
        <!-- <section id="thebros" ref="thebros"><TheBros/></section> -->
        <section id="roadmap" ref="roadmap"><Roadmap/></section>
        <section id="team" ref="team"><Team/></section>
        <section id="faq" ref="faq"><Faq/></section>
      </div>
    </div>
    <Footer/>
  </div>
</template>
<script>

import Gheader from './Gheader.vue'
import Nav from './Nav.vue'
import Welcome from './Welcome.vue'
import About from './About.vue'
// import TheBros from './TheBros.vue'
import Roadmap from './Roadmap.vue'
import Team from './Team.vue'
import Faq from './Faq.vue'
import Footer from './Footer.vue'

export default {
  components: {
    Nav, Gheader, Welcome, About, Roadmap, Team, Faq, Footer
  },
  methods: {
    gotoSection (sec) {
      const top = sec === 'top' ? 0 : (this.$refs[sec].offsetTop - 120)
      window.scrollTo({
        top: top,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.sections {
  // padding-top: 96px;
  .sizedBody {
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 5%;
    overflow: hidden;
    section {
      margin-top: 50px;
    }
  }
}
