<template>
  <div class="faq" ref="faq">
    <h1 class="faq__title">FAQ</h1>
    <div class="faq__cards">
      <article class="faq__cards--card" v-for="(faq, idx) in faqs" :key="idx" :class="{ 'faq__cards--card-opened': faq.open }" @click="openQuestion(idx)" :ref="`card${idx}`">
        <div class="faq__cards--card-title" :ref="`card-title-${idx}`">
          <div>{{faq.question}}</div> <div class="faq__cards--card-title-up">{{faq.open? '-' : '+'}}</div>
        </div>
        <p class="faq__cards--card-txt" :ref="`card-txt-${idx}`">{{faq.answer}}</p>
      </article>
    </div>
  </div>
</template>
<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

export default {
  mounted () {
    for (let i = 0; i < this.faqs.length; i++) {
      this.closeQuestion(i)
    }

    gsap.timeline({
      scrollTrigger: {
        trigger: '#team',
        start: 'top',
        // markers: true,
        scrub: true
      }
    })
      .fromTo(this.$refs.faq, { autoAlpha: 0, x: -100 }, { autoAlpha: 1, x: 0 }, 0)
  },
  methods: {
    closeQuestion (index) {
      this.faqs[index].open = false
      this.$refs[`card${index}`][0].style.height = `${(this.$refs[`card-title-${index}`][0].clientHeight - 5)}px`
    },
    _openQuestion (index) {
      this.faqs[index].open = true
      this.$refs[`card${index}`][0].style.height = `${(this.$refs[`card-title-${index}`][0].clientHeight + this.$refs[`card-txt-${index}`][0].clientHeight)}px`
    },
    openQuestion (index) {
      if (this.lastIndex !== index) this.closeQuestion(this.lastIndex)
      this.lastIndex = index
      if (!this.faqs[index].open) this._openQuestion(this.lastIndex)
      else this.closeQuestion(this.lastIndex)
    }
  },
  data () {
    return {
      lastIndex: 0,
      faqs: [
        {
          question: 'How much is a Gunner Bro?',
          answer: 'The price of a Gunner Bro will be announced as we move closer to launch.',
          open: true
        },
        {
          question: 'Will there be a presale or whitelist?',
          answer: 'Presales are for pansies, but we have created a few pathways to join the elite squad and become eligible for the whitelist.',
          open: false
        },
        {
          question: 'Where will I mint my Gunner Bro',
          answer: 'On mint day everyone will go to the main website to mint. We are working on a strategy to ensure that gas doesnt discourage out community or slow down our mint process',
          open: false
        },
        {
          question: 'What can I do with my Gunner Bro NFT?',
          answer: 'Initially, they are now your IP to use as you desire for swagger and bragging rights. Soon after reveal, you will receive a skin of your NFT that will be used in the playable shooter to earn real rewards. You will also be eligible for an airdrop of the Brass token and continued token rewards the longer you hold your Bro.',
          open: false
        },
        {
          question: 'Is the game on the website the same game playable with my NFT?',
          answer: 'No, the initial beta game is a placeholder and way for everyone to stay engaged as well as track winners for giveaways. The P2E NFT game will be designed new and fresh with direction from our community and on a sustainable network.',
          open: false
        },
        {
          question: 'Why join Gunner Bros',
          answer: 'An ever-expanding Gunner multiverse is our goal with Bros being the alpha dogs and early gunners will benefit every step of the way. Our goal is to develop a kick ass community of gamers and collectors as well as design a place for gaming to lead to new connections and teammates. Collecting a Gunner Bro is the beginning of an ever-expanding multiverse and we look forward to seeing you there.',
          open: false
        }
      ]
    }
  }
}
</script>

