<template>
  <div ref="introRef" class="intro" :class="animaai">
    <!-- <div class="intro__img " :style="`background-image: url(${introgif})`"></div>
    <div class="intro__img intro__img-one" :style="`background-image: url(${introgif})`"></div>
    <div class="intro__img intro__img-two" :style="`background-image: url(${introgif})`"></div>
    <div class="intro__lines"></div> -->
    <video ref="introVd" class="intro__vd" autoplay>
      <source :src="introVd" type="video/mp4">
    </video>

    <div class="intro__skip" @click="clickIntro()"> <h1>Skip intro</h1></div>
  </div>
</template>
<script>
import { gsap } from 'gsap'

const intro = require('@/assets/intro/introanime.gif')
const _introVd = require('@/assets/intro/gunner_intro.mp4')

export default {
  name: 'Intro',
  data () {
    return {
      introgif: intro,
      animaaiclass: 'animaii',
      animeDelay: 1000,
      animaai: '',
      introVd: _introVd
    }
  },
  mounted () {
    this.$refs.introVd.muted = true
    this.$refs.introVd.onended = this.clickIntro
    this.$refs.introVd.play()

    console.log('this.$refs.introVd', this.$refs.introVd)
  },
  created () {
    // this.addAnimaii()
  },
  methods: {
    clickIntro () {
      gsap.to(this.$refs.introRef, {
        autoAlpha: 0,
        onComplete: () => {
          this.$emit('skipIntro')
        }
      })
    },
    addAnimaii () {
      // this.animaai = this.animaaiclass;
      // const rananim = this.animeDelay * (Math.floor(Math.random() * 50) + 2);
      setTimeout(() => {
        this.animaai = this.animaaiclass
        this.removeAnimaii()
      }, 1000)
    },
    removeAnimaii () {
      // this.animaai = '';
      setTimeout(() => {
        this.animaai = ''
        this.addAnimaii()
      }, 2000)
    }
  }
}
</script>
