<template>
  <nav class="nav" ref="nav">

    <div class="nav__container">
      <div class='nav__logo' @click="itemClick('top')" :style="{'background-image': `url(${logo})`}"></div>
      <ul class="nav__menu">
        <li class="nav__menu--item" @click="itemClick('welcome')">Welcome</li>
        <li class="nav__menu--item" @click="itemClick('about')">About</li>
        <li class="nav__menu--item" @click="itemClick('roadmap')">Roadmap</li>
        <li class="nav__menu--item" @click="itemClick('team')">Team</li>
        <li class="nav__menu--item" @click="itemClick('faq')">Faq</li>
      </ul>
      <div class="nav__redes">
          <img class="nav__redes--icon" :src="instagram" @click="goInstagram()">
          <img class="nav__redes--icon" :src="twitter" @click="goTwitter()">
          <img class="nav__redes--icon" :src="discord" @click="goDiscord()">
      </div>
    </div>
  </nav>
</template>
<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

const logoF = require('@/assets/logo_final.svg')
const _instagram = require('@/assets/networks/instagram-inverted.svg')
const _twitter = require('@/assets/networks/twitter-inverted.svg')
const _discord = require('@/assets/networks/discord.svg')

export default {
  name: 'Nav',
  data () {
    return {
      logo: logoF,
      instagram: _instagram,
      twitter: _twitter,
      discord: _discord
    }
  },
  methods: {
    itemClick (sec) {
      this.$emit('menuClick', sec)
    },
    goInstagram () {
      window.open('https://www.instagram.com/gunnerbrosnft/', '_blank')
    },
    goTwitter () {
      window.open('https://twitter.com/Gunner_Bros', '_blank')
    },
    goDiscord () {
      window.open('https://discord.gg/J3jEzXAhNu', '_blank')
    }
  },
  mounted () {
    gsap.timeline({
      scrollTrigger: {
        trigger: '#header',
        start: 'bottom-=200',
        // markers: true,
        scrub: true
      }
    })
      .fromTo(this.$refs.nav, { backgroundColor: 'transparent' }, { backgroundColor: 'white' }, 0)
  }
}
</script>
